@charset "UTF-8";
:root {
  --c-brand: #3eaf7c;
  --c-brand-light: #4abf8a;
  --c-bg: #ffffff;
  --c-bg-light: #f3f4f5;
  --c-bg-lighter: #eeeeee;
  --c-bg-navbar: var(--c-bg);
  --c-bg-sidebar: var(--c-bg);
  --c-bg-arrow: #cccccc;
  --c-text: #2c3e50;
  --c-text-accent: var(--c-brand);
  --c-text-light: #3a5169;
  --c-text-lighter: #4e6e8e;
  --c-text-lightest: #6a8bad;
  --c-text-quote: #999999;
  --c-border: #eaecef;
  --c-border-dark: #dfe2e5;
  --c-tip: #42b983;
  --c-tip-bg: var(--c-bg-light);
  --c-tip-title: var(--c-text);
  --c-tip-text: var(--c-text);
  --c-tip-text-accent: var(--c-text-accent);
  --c-warning: #e7c000;
  --c-warning-bg: #fffae3;
  --c-warning-title: #ad9000;
  --c-warning-text: #746000;
  --c-warning-text-accent: var(--c-text);
  --c-danger: #cc0000;
  --c-danger-bg: #ffe0e0;
  --c-danger-title: #990000;
  --c-danger-text: #660000;
  --c-danger-text-accent: var(--c-text);
  --c-details-bg: #eeeeee;
  --c-badge-tip: var(--c-tip);
  --c-badge-warning: var(--c-warning);
  --c-badge-danger: var(--c-danger);
  --t-color: 0.3s ease;
  --t-transform: 0.3s ease;
  --code-bg-color: #282c34;
  --code-hl-bg-color: rgba(0, 0, 0, 0.66);
  --code-ln-color: #9e9e9e;
  --code-ln-wrapper-width: 3.5rem;
  --font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  --font-family-code: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  --navbar-height: 3.6rem;
  --navbar-padding-v: 0.7rem;
  --navbar-padding-h: 1.5rem;
  --sidebar-width: 20rem;
  --sidebar-width-mobile: calc(var(--sidebar-width) * 0.82);
  --content-width: 740px;
  --homepage-width: 960px;
}

.back-to-top {
  --back-to-top-color: var(--c-brand);
  --back-to-top-color-hover: var(--c-brand-light);
}

.DocSearch {
  --docsearch-primary-color: var(--c-brand);
  --docsearch-text-color: var(--c-text);
  --docsearch-highlight-color: var(--c-brand);
  --docsearch-muted-color: var(--c-text-quote);
  --docsearch-container-background: rgba(9, 10, 17, 0.8);
  --docsearch-modal-background: var(--c-bg-light);
  --docsearch-searchbox-background: var(--c-bg-lighter);
  --docsearch-searchbox-focus-background: var(--c-bg);
  --docsearch-searchbox-shadow: inset 0 0 0 2px var(--c-brand);
  --docsearch-hit-color: var(--c-text-light);
  --docsearch-hit-active-color: var(--c-bg);
  --docsearch-hit-background: var(--c-bg);
  --docsearch-hit-shadow: 0 1px 3px 0 var(--c-border-dark);
  --docsearch-footer-background: var(--c-bg);
}

.external-link-icon {
  --external-link-icon-color: var(--c-text-quote);
}

.medium-zoom-overlay {
  --medium-zoom-bg-color: var(--c-bg);
}

#nprogress {
  --nprogress-color: var(--c-brand);
}

.pwa-popup {
  --pwa-popup-text-color: var(--c-text);
  --pwa-popup-bg-color: var(--c-bg);
  --pwa-popup-border-color: var(--c-brand);
  --pwa-popup-shadow: 0 4px 16px var(--c-brand);
  --pwa-popup-btn-text-color: var(--c-bg);
  --pwa-popup-btn-bg-color: var(--c-brand);
  --pwa-popup-btn-hover-bg-color: var(--c-brand-light);
}

.search-box {
  --search-bg-color: var(--c-bg);
  --search-accent-color: var(--c-brand);
  --search-text-color: var(--c-text);
  --search-border-color: var(--c-border);
  --search-item-text-color: var(--c-text-lighter);
  --search-item-focus-bg-color: var(--c-bg-light);
}

html.dark {
  --c-brand: #3aa675;
  --c-brand-light: #349469;
  --c-bg: #22272e;
  --c-bg-light: #2b313a;
  --c-bg-lighter: #262c34;
  --c-text: #adbac7;
  --c-text-light: #96a7b7;
  --c-text-lighter: #8b9eb0;
  --c-text-lightest: #8094a8;
  --c-border: #3e4c5a;
  --c-border-dark: #34404c;
  --c-tip: #318a62;
  --c-warning: #ceab00;
  --c-warning-bg: #7e755b;
  --c-warning-title: #ceac03;
  --c-warning-text: #362e00;
  --c-danger: #940000;
  --c-danger-bg: #806161;
  --c-danger-title: #610000;
  --c-danger-text: #3a0000;
  --c-details-bg: #323843;
  --code-hl-bg-color: #363b46;
}

html.dark .DocSearch {
  --docsearch-logo-color: var(--c-text);
  --docsearch-modal-shadow: inset 1px 1px 0 0 #2c2e40, 0 3px 8px 0 #000309;
  --docsearch-key-shadow: inset 0 -2px 0 0 #282d55, inset 0 0 1px 1px #51577d,
    0 2px 2px 0 rgba(3, 4, 9, 0.3);
  --docsearch-key-gradient: linear-gradient(-225deg, #444950, #1c1e21);
  --docsearch-footer-shadow: inset 0 1px 0 0 rgba(73, 76, 106, 0.5),
    0 -4px 8px 0 rgba(0, 0, 0, 0.2);
}

html,
body {
  padding: 0;
  margin: 0;
  background-color: var(--c-bg);
  transition: background-color var(--t-color);
}

html.dark {
  color-scheme: dark;
}

html {
  font-size: 16px;
}

body {
  font-family: var(--font-family);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1rem;
  color: var(--c-text);
}

a {
  font-weight: 500;
  color: var(--c-text-accent);
  text-decoration: none;
  overflow-wrap: break-word;
}

p a code {
  font-weight: 400;
  color: var(--c-text-accent);
}

kbd {
  font-family: var(--font-family-code);
  color: var(--c-text);
  background: var(--c-bg-lighter);
  border: solid 0.15rem var(--c-border-dark);
  border-bottom: solid 0.25rem var(--c-border-dark);
  border-radius: 0.15rem;
  padding: 0 0.15em;
}

code {
  font-family: var(--font-family-code);
  color: var(--c-text-lighter);
  padding: 0.25rem 0.5rem;
  margin: 0;
  font-size: 0.85em;
  background-color: var(--c-bg-lighter);
  border-radius: 3px;
  overflow-wrap: break-word;
  transition: background-color var(--t-color);
}

blockquote {
  font-size: 1rem;
  color: var(--c-text-quote);
  border-left: 0.2rem solid var(--c-border-dark);
  margin: 1rem 0;
  padding: 0.25rem 0 0.25rem 1rem;
}
blockquote > p {
  margin: 0;
}

ul,
ol {
  padding-left: 1.2em;
}

strong {
  font-weight: 600;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  line-height: 1.25;
}
h1:focus-visible,
h2:focus-visible,
h3:focus-visible,
h4:focus-visible,
h5:focus-visible,
h6:focus-visible {
  outline: none;
}
h1:hover .header-anchor,
h2:hover .header-anchor,
h3:hover .header-anchor,
h4:hover .header-anchor,
h5:hover .header-anchor,
h6:hover .header-anchor {
  opacity: 1;
}

h1 {
  font-size: 2.2rem;
}

h2 {
  font-size: 1.65rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid var(--c-border);
  transition: border-color var(--t-color);
}

h3 {
  font-size: 1.35rem;
}

h4 {
  font-size: 1.15rem;
}

h5 {
  font-size: 1.05rem;
}

h6 {
  font-size: 1rem;
}

a.header-anchor {
  font-size: 0.85em;
  float: left;
  margin-left: -0.87em;
  padding-right: 0.23em;
  margin-top: 0.125em;
  opacity: 0;
}
a.header-anchor:hover {
  text-decoration: none;
}
a.header-anchor:focus-visible {
  opacity: 1;
}

p,
ul,
ol {
  line-height: 1.7;
}

hr {
  border: 0;
  border-top: 1px solid var(--c-border);
}

table {
  border-collapse: collapse;
  margin: 1rem 0;
  display: block;
  overflow-x: auto;
  transition: border-color var(--t-color);
}

tr {
  border-top: 1px solid var(--c-border-dark);
  transition: border-color var(--t-color);
}
tr:nth-child(2n) {
  background-color: var(--c-bg-light);
  transition: background-color var(--t-color);
}

th,
td {
  padding: 0.6em 1em;
  border: 1px solid var(--c-border-dark);
  transition: border-color var(--t-color);
}

.arrow {
  display: inline-block;
  width: 0;
  height: 0;
}
.arrow.up {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 6px solid var(--c-bg-arrow);
}
.arrow.down {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid var(--c-bg-arrow);
}
.arrow.right {
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 6px solid var(--c-bg-arrow);
}
.arrow.left {
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-right: 6px solid var(--c-bg-arrow);
}

.badge {
  display: inline-block;
  font-size: 14px;
  height: 18px;
  line-height: 18px;
  border-radius: 3px;
  padding: 0 6px;
  color: var(--c-bg);
  vertical-align: top;
  transition: color var(--t-color), background-color var(--t-color);
}
.badge.tip {
  background-color: var(--c-badge-tip);
}
.badge.warning {
  background-color: var(--c-badge-warning);
}
.badge.danger {
  background-color: var(--c-badge-danger);
}
.table-of-contents .badge {
  vertical-align: middle;
}
.badge + .badge {
  margin-left: 5px;
}

code[class*=language-],
pre[class*=language-] {
  color: #ccc;
  background: none;
  font-family: var(--font-family-code);
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Code blocks */
pre[class*=language-] {
  padding: 1em;
  margin: 0.5em 0;
  overflow: auto;
}

:not(pre) > code[class*=language-],
pre[class*=language-] {
  background: #2d2d2d;
}

/* Inline code */
:not(pre) > code[class*=language-] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
}

.token.comment,
.token.block-comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #999;
}

.token.punctuation {
  color: #ccc;
}

.token.tag,
.token.attr-name,
.token.namespace,
.token.deleted {
  color: #ec5975;
}

.token.function-name {
  color: #6196cc;
}

.token.boolean,
.token.number,
.token.function {
  color: #f08d49;
}

.token.property,
.token.class-name,
.token.constant,
.token.symbol {
  color: #f8c555;
}

.token.selector,
.token.important,
.token.atrule,
.token.keyword,
.token.builtin {
  color: #cc99cd;
}

.token.string,
.token.char,
.token.attr-value,
.token.regex,
.token.variable {
  color: #7ec699;
}

.token.operator,
.token.entity,
.token.url {
  color: #67cdcc;
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.token.inserted {
  color: #3eaf7c;
}

.theme-default-content pre,
.theme-default-content pre[class*=language-] {
  line-height: 1.4;
  padding: 1.3rem 1.5rem;
  margin: 0.85rem 0;
  border-radius: 6px;
  overflow: auto;
}
.theme-default-content pre code,
.theme-default-content pre[class*=language-] code {
  color: #fff;
  padding: 0;
  background-color: transparent;
  border-radius: 0;
  overflow-wrap: unset;
  -webkit-font-smoothing: auto;
  -moz-osx-font-smoothing: auto;
}
.theme-default-content .line-number {
  font-family: var(--font-family-code);
}

div[class*=language-] {
  position: relative;
  background-color: var(--code-bg-color);
  border-radius: 6px;
}
div[class*=language-]::before {
  position: absolute;
  z-index: 3;
  top: 0.8em;
  right: 1em;
  font-size: 0.75rem;
  color: var(--code-ln-color);
}
div[class*=language-] pre,
div[class*=language-] pre[class*=language-] {
  background: transparent !important;
  position: relative;
  z-index: 1;
}
div[class*=language-] .highlight-lines {
  user-select: none;
  padding-top: 1.3rem;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  line-height: 1.4;
}
div[class*=language-] .highlight-lines .highlight-line {
  background-color: var(--code-hl-bg-color);
}
div[class*=language-]:not(.line-numbers-mode) .line-numbers {
  display: none;
}
div[class*=language-].line-numbers-mode .highlight-lines .highlight-line {
  position: relative;
}
div[class*=language-].line-numbers-mode .highlight-lines .highlight-line::before {
  content: " ";
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  display: block;
  width: var(--code-ln-wrapper-width);
  height: 100%;
}
div[class*=language-].line-numbers-mode pre {
  margin-left: var(--code-ln-wrapper-width);
  padding-left: 1rem;
  vertical-align: middle;
}
div[class*=language-].line-numbers-mode .line-numbers {
  position: absolute;
  top: 0;
  width: var(--code-ln-wrapper-width);
  text-align: center;
  color: var(--code-ln-color);
  padding-top: 1.25rem;
  line-height: 1.4;
  counter-reset: line-number;
}
div[class*=language-].line-numbers-mode .line-numbers .line-number {
  position: relative;
  z-index: 3;
  user-select: none;
  height: 1.4em;
}
div[class*=language-].line-numbers-mode .line-numbers .line-number::before {
  counter-increment: line-number;
  content: counter(line-number);
  font-size: 0.85em;
}
div[class*=language-].line-numbers-mode::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: var(--code-ln-wrapper-width);
  height: 100%;
  border-radius: 6px 0 0 6px;
  border-right: 1px solid var(--code-hl-bg-color);
}

div[class*=language-].ext-c:before {
  content: "c";
}

div[class*=language-].ext-cpp:before {
  content: "cpp";
}

div[class*=language-].ext-cs:before {
  content: "cs";
}

div[class*=language-].ext-css:before {
  content: "css";
}

div[class*=language-].ext-dart:before {
  content: "dart";
}

div[class*=language-].ext-docker:before {
  content: "docker";
}

div[class*=language-].ext-fs:before {
  content: "fs";
}

div[class*=language-].ext-go:before {
  content: "go";
}

div[class*=language-].ext-html:before {
  content: "html";
}

div[class*=language-].ext-java:before {
  content: "java";
}

div[class*=language-].ext-js:before {
  content: "js";
}

div[class*=language-].ext-json:before {
  content: "json";
}

div[class*=language-].ext-kt:before {
  content: "kt";
}

div[class*=language-].ext-less:before {
  content: "less";
}

div[class*=language-].ext-makefile:before {
  content: "makefile";
}

div[class*=language-].ext-md:before {
  content: "md";
}

div[class*=language-].ext-php:before {
  content: "php";
}

div[class*=language-].ext-py:before {
  content: "py";
}

div[class*=language-].ext-rb:before {
  content: "rb";
}

div[class*=language-].ext-rs:before {
  content: "rs";
}

div[class*=language-].ext-sass:before {
  content: "sass";
}

div[class*=language-].ext-scss:before {
  content: "scss";
}

div[class*=language-].ext-sh:before {
  content: "sh";
}

div[class*=language-].ext-styl:before {
  content: "styl";
}

div[class*=language-].ext-ts:before {
  content: "ts";
}

div[class*=language-].ext-toml:before {
  content: "toml";
}

div[class*=language-].ext-vue:before {
  content: "vue";
}

div[class*=language-].ext-yml:before {
  content: "yml";
}

@media (max-width: 419px) {
  .theme-default-content div[class*=language-] {
    margin: 0.85rem -1.5rem;
    border-radius: 0;
  }
}
/**
 * code-group
 */
.code-group__nav {
  margin-top: 0.85rem;
  margin-bottom: calc(-1.7rem - 6px);
  padding-bottom: calc(1.7rem - 6px);
  padding-left: 10px;
  padding-top: 10px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background-color: var(--code-bg-color);
}

.code-group__ul {
  margin: auto 0;
  padding-left: 0;
  display: inline-flex;
  list-style: none;
}

.code-group__nav-tab {
  border: 0;
  padding: 5px;
  cursor: pointer;
  background-color: transparent;
  font-size: 0.85em;
  line-height: 1.4;
  color: rgba(255, 255, 255, 0.9);
  font-weight: 600;
}

.code-group__nav-tab:focus {
  outline: none;
}

.code-group__nav-tab:focus-visible {
  outline: 1px solid rgba(255, 255, 255, 0.9);
}

.code-group__nav-tab-active {
  border-bottom: var(--c-brand) 1px solid;
}

@media (max-width: 419px) {
  .code-group__nav {
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    border-radius: 0;
  }
}
/**
 * code-group-item
 */
.code-group-item {
  display: none;
}

.code-group-item__active {
  display: block;
}

.code-group-item > pre {
  background-color: orange;
}

.custom-container {
  transition: color var(--t-color), border-color var(--t-color), background-color var(--t-color);
}
.custom-container .custom-container-title {
  font-weight: 600;
}
.custom-container .custom-container-title:not(:only-child) {
  margin-bottom: -0.4rem;
}
.custom-container.tip, .custom-container.warning, .custom-container.danger {
  padding: 0.1rem 1.5rem;
  border-left-width: 0.5rem;
  border-left-style: solid;
  margin: 1rem 0;
}
.custom-container.tip {
  border-color: var(--c-tip);
  background-color: var(--c-tip-bg);
  color: var(--c-tip-text);
}
.custom-container.tip .custom-container-title {
  color: var(--c-tip-title);
}
.custom-container.tip a {
  color: var(--c-tip-text-accent);
}
.custom-container.warning {
  border-color: var(--c-warning);
  background-color: var(--c-warning-bg);
  color: var(--c-warning-text);
}
.custom-container.warning .custom-container-title {
  color: var(--c-warning-title);
}
.custom-container.warning a {
  color: var(--c-warning-text-accent);
}
.custom-container.danger {
  border-color: var(--c-danger);
  background-color: var(--c-danger-bg);
  color: var(--c-danger-text);
}
.custom-container.danger .custom-container-title {
  color: var(--c-danger-title);
}
.custom-container.danger a {
  color: var(--c-danger-text-accent);
}
.custom-container.details {
  display: block;
  position: relative;
  border-radius: 2px;
  margin: 1.6em 0;
  padding: 1.6em;
  background-color: var(--c-details-bg);
}
.custom-container.details h4 {
  margin-top: 0;
}
.custom-container.details figure:last-child,
.custom-container.details p:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
}
.custom-container.details summary {
  outline: none;
  cursor: pointer;
}

.home {
  padding: var(--navbar-height) 2rem 0;
  max-width: var(--homepage-width);
  margin: 0px auto;
  display: block;
}
.home .hero {
  text-align: center;
}
.home .hero img {
  max-width: 100%;
  max-height: 280px;
  display: block;
  margin: 3rem auto 1.5rem;
}
.home .hero h1 {
  font-size: 3rem;
}
.home .hero h1,
.home .hero .description,
.home .hero .actions {
  margin: 1.8rem auto;
}
.home .hero .actions {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}
.home .hero .description {
  max-width: 35rem;
  font-size: 1.6rem;
  line-height: 1.3;
  color: var(--c-text-lightest);
}
.home .hero .action-button {
  display: inline-block;
